//require('normalize.css/normalize.css');
require('styles/styles.css');
require('styles/monokai-sublime.css');
require('styles/App.css');

import React from 'react';
import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import {
	Promise
} from 'es6-promise';

import Index from './route/Index';

class AppComponent extends React.Component {

	constructor(props) {
		super(props);
		this.state = {
			types : [],
			type : 0,
		};
	}


	componentDidMount() {
		var proto = window.location.protocol.split(":")[0];
		var host = proto + "://" + window.location.host;
		host = "https://www.mark-down.cn";
		var url = host + "/api/v1/type";
		fetch(url)
			.then((response) => {
				if(response.ok)
					return response.json();
			})
			.then((json) => {
				this.setState({
					types: json.types
				});
			})
			.catch((e) => {
			});
	}

	setType(id) {
		if (this.state.type == id) {
			return ;
		}

		this.setState({
			type: id
		});
	}

	typeChanged() {
		return this.state.type;
	}

  render() {
		var typesList = this.state.types.map((type) => {
			return (
				<ReactCSSTransitionGroup
						key={type.id}
						transitionName="type-list"
						transitionAppear={true}
						transitionAppearTimeout = { 1000 }
						transitionLeaveTimeout={ 500 }
						transitionEnterTimeout={ 500 }>
						<li
							onClick={ () => { this.setType(type.id); } }
							key={type.id}
							className={ this.state.type === type.id ? 'type-list-item active':'type-list-item' }>
							<a href='javascript:void(0)'>{type.name}</a>
						</li>
				</ReactCSSTransitionGroup>
				)
		});

    return (
			<div>
				<header>
					<div className='container'>
						<div className='row'>
							<div className='col-md-12'>
								<div className='brand'>
									<a href='/'>码可档</a>
								</div>
								<div className='type-list'>
									<ul className='type-list-wrapper'>
										<li
											onClick={ ()=> { this.setType(0) } }
											className={ this.state.type==0?'type-list-item active':'type-list-item' }>
												<a href='javascript:void(0)'>全部</a>
										</li>
										{typesList}
									</ul>
								</div>
							</div>
						</div>
					</div>
				</header>
				<div className='content'>
					<Index typeChanged = { this.typeChanged.bind(this) } />
				</div>
				<footer>
					<div className='container'>
						<div className='col-md-12'>
						<div className='copyright'>码可档 &copy; 2016-2017<a href="https://github.com/ohmountain" target="_blank"> 仁山 </a></div>
						</div>
					</div>
				</footer>
			</div>
    );
  }
}

AppComponent.defaultProps = {
};

export default AppComponent;



