import React,{	Component } from 'react';
import { Promise } from 'es6-promise';

import ReactCSSTransitionGroup from 'react-addons-css-transition-group';
import HighLight from 'react-highlight';

import ScrollUp from 'react-scroll-up';

import {
    scroller
} from 'react-scroll';

export default class Index extends Component {

	constructor(props) {
		super(props);

		var { typeChanged } = this.props;

		var type = typeChanged();

		if (type === undefined) {
			type = 0;
		}

		this.state = {
			blogs: [],
			page: 0,
			type: type,
			limit: 10,
			maxPage: 1,
		};

	}

	componentDidMount() {
		document.getElementById('loading').className = 'fadeout';
		var t = setTimeout(() => {
			document.getElementById('loading').style.opacity = "0";
			document.body.removeChild(document.getElementById('loading'));
			document.getElementById('app').className='fadein';
			//document.getElementById('app').style.opacity = "1";
			clearTimeout(t);
			document.getElementById('app').style.display = "block";
		}, 1000);
		//this.getBlogs(0, this.state.page+1, this.state.limit);
	}

	componentWillReceiveProps(props, p) {

		if (typeof(hljs) !== 'undefined') {
				hljs.configure({
					tabReplace: ' ', // 4 spaces
			})
		}

		let { typeChanged } = this.props;

		this.setState({
			type: typeChanged(),
			maxPage: 1
		});

		this.getBlogs(typeChanged(), 1, this.state.limit);
	}


	getBlogs(type=0, page=1, limit=10) {

		page = parseInt(page);

		if (page < 1) {
			return;
		}

		if (page > this.state.maxPage) {
			return;
		}

		this.setState({
			blogs: []
		});



		var method = {
			method: 'POST'
		};

		var headers = {
			"Content-Type": "application/x-www-form-urlencoded"
		};

		var body = `type=${type}&page=${page}&limit=${limit}`;

		var proto = window.location.protocol.split(":")[0];
		var host = proto + '://' +window.location.host;
		host = "https://www.mark-down.cn";
		var url = host + '/api/v1/blog';

		fetch(url,{
				method: 'POST',
					headers,
					body
				})
			.then((response) => {
				if(response.ok)
					return response.json();
			})
			.then((json) => {

				this.setState({
					page: json.page,
					maxPage: json.maxPage == 0 ? 1 : json.maxPage,
					blogs: json.blogs,
					limit: limit
				});

       this.refs.toUp.handleClick();

			})
			.catch((e) => {
				console.error(e);
			})

		let maxPage = this.state.maxPage;

		this.setState({
			page
		});
	}

	render() {

		var key = 1;
		var blogList = this.state.blogs.map(b => {
						return <ReactCSSTransitionGroup
									key = {b.id}
									transitionName="blog-item"
									transitionAppear={true}
									transitionAppearTimeout = { 1000 }
									transitionLeaveTimeout={ 500 }
									transitionEnterTimeout={ 1000 }>
						<BlogComponent key={key++} blog={b} />
						</ReactCSSTransitionGroup>
		});

		return (<div>
				<div className='container'>
					<div className='row'>
						<div className='col-md-12'>
							<div className='blog-list'>
								{ blogList }
								{
									blogList.length > 0 ?
									<PagerComponent 
										page={this.state.page}
										maxPage={this.state.maxPage}
										getBlogs={ this.getBlogs.bind(this) }
										type={this.state.type}
										limit={this.state.limit} />
											: <NotFound />
								}
						</div>

            <ScrollUp ref='toUp' showUnder={160} style={{
                width: '48px',
                height: '48px',
                lineHeight: '48px',
                backgroundColor: '#F0F0F0',
                textAlign: 'center',
                color: '#444',
                fontSize: '24px',
                position: 'fixed',
                bottom: 72,
                right: 30,
                cursor: 'pointer',
                transitionDuration: '0.2s',
                transitionTimingFunction: 'linear',
                transitionDelay: '0s',
                borderStyle: 'solid',
                borderWidth: '1px',
                borderColor: '#E0E0E0',
                borderRadius: '4px',
                zIndex: 89,
            }}>
            <span>☝</span>
            </ScrollUp>

						</div>
					</div>
				</div>
        <IndicatorComponent blogs={this.state.blogs} />
		</div>);
	}
}

class BlogComponent extends Component {
	render() {
		return (
			<div className='blog-item' name={`${this.props.blog.title}`} id={`${this.props.blog.title}`}>
				<div className='head'>
					<div className='title'>{ this.props.blog.title }</div>
					<div className='type'>{ this.props.blog.type.name }</div>
				</div>
				<div className='body' ref='body'>
					<HighLight innerHTML={true}>{this.props.blog.content}</HighLight>
				</div>
				<div className='foot'>
					<div className='info'>
						<div className='author'>renshan</div>
						<div className='time'>{ this.props.blog.time }</div>
					</div>
				</div>
			</div>
			)
	}
}

class IndicatorComponent extends Component {

    handleHover() {
        return '&spades;';
    }

    render() {
        let { blogs } = this.props;
        if (blogs == null) {
            blogs = [];
        }

        const indicators = blogs.map(blog => {
            return <IndicatorItemComponent blog={blog} key={blog.id} />;
        });

        return <div className="indicator"><div className="indicator-list">{ indicators }</div></div>;
    }
}

class IndicatorItemComponent extends Component {

    constructor() {
        super();
        this.state = { icon: '♣' };
    }

    handleHover() {
        this.setState({ icon: '♠' });
    }

    handleUnHover() {
        this.setState({ icon: '♣' });
    }

    handleClick() {
        scroller.scrollTo(this.props.blog.title, {
            duration: 450,
            delay: 50,
            smooth: true
        });
    }

    render() {
        return (<a onClick={ this.handleClick.bind(this) } onMouseOver={ this.handleHover.bind(this) } onMouseOut={ this.handleUnHover.bind(this) }  ><div className="indicator-title">{ this.props.blog.title }</div><div className="indicator-anchor">{this.state.icon}</div></a>);
    }
}


class PagerComponent extends Component {

	constructor(props) {
		super(props);
	}

	getBlogs(type, page, limit) {
		this.props.getBlogs(type, page, limit);
	}

	render() {

		var pageList = [];
		var number = 5;
		var leftFix = 0;
		var rightFix = 0;
		var leftList = [];

		var { type, page, limit } = this.props;

		// 靠左
		if(this.props.page - 2 <= 0) {
			rightFix = 3 - this.props.page;
		}


		// 靠右
		if(this.props.page + 2 > this.props.maxPage) {
			leftFix = this.props.page + 2 - this.props.maxPage;
		}

		// lt
		var lt = <li onClick={ ()=>{ this.getBlogs(type, page-1,limit) } } key={'lt'} className={ this.props.page === 1 ? 'disabled' : '' }><a>&lt;</a></li>;
		pageList.push(lt);

		// left
		for(var i = this.props.page - 1;(i >= this.props.page - 2 - leftFix)&& (i > 0); i--) {
			var tmp = <li onClick={ (e)=>{ this.getBlogs(type, e.target.innerText, limit) } } key={i}><a>{i}</a></li>;
			leftList.push(tmp);
		}

		for(var i = leftList.length -1; i >=0; i--) {
			pageList.push(leftList[i]);
		}

		// current
		var current = <li key={'current'} className='current'><a>{this.props.page}</a></li>;
		pageList.push(current);

		// right
		for(var i = this.props.page + 1; (i <= this.props.maxPage) && (i <= this.props.page + 2 + rightFix); i++) {
			var tmp = <li onClick={ (i)=>{ this.getBlogs(type, i.target.innerText, limit) } } key={i}><a>{i}</a></li>;
			pageList.push(tmp);
		}


		// gt
		var gt = <li onClick={ ()=>{ this.getBlogs(type, page+1, limit) } } key={'gt'} className={ this.props.page === this.props.maxPage ? 'disabled' : '' }><a>&gt;</a></li>;
		pageList.push(gt);


		return (<div className='i-pager'>
				<ul className='i-pager-wrapper'>{ pageList }</ul>
		</div>)
	}
}

class NotFound extends Component {
	render() {
		return <div className='empty'>... loading  ...</div>
	}
}



